import { useMemo } from "react";
import { Link, useFetcher, useLoaderData, useTransition } from "remix";
import {boards,cards} from "../services/trellotypes"

export let loader =async () => {
  // return await getListsOnBoard()
   
      let group =[{
        title:"INOVAÇÃO",
        data:[]
      }];

      let trashColumn = boards.find((e) => e.name != "DESCARTADOS");
      let data = cards.filter((e) => e.idList != trashColumn.id);
      data.forEach(e=>{
        if (e.labels.indexOf((ee) => ee.name === "APLICATIVO") > -1) {
          console.log(e);
          group[0].data.push(e);
        }
      })
  return {data}
};

export default function Index() {
    let {data} = useLoaderData();

  return (
    <div style={{
      flexDirection:"row",
      display: "flex"
    }}>
      {data?.map(e=>{
        return <div style={{
          flexDirection:"column",
          display: "flex"
        }}>
              {e.title}
              {e?.data?.map(e=>{
                return <span>{e.name}</span>
              })}
          </div>
      })}
    </div>
  );
}
